<script>
  import Users from "./Users";
  import Confirmation from "./Confirmation";

  export default {
    name: "Schedulely",
    components: {
      Users,
      Confirmation,
    },
    data() {
      return {
        isConfirmed: false,
        userName: "",
      };
    },
    mounted() {
      this.$on("bookSlotConfirmed", (response, userName) => {
        this.isConfirmed = response;
        this.userName = userName;
      });
    },
    methods: {},
  };
