<template>
  <div>
    <b-card v-if="!showConfirm">
      <b-card-body no-body class="p-0" v-if="!pageError">
        <div>
          <h4 class="mb-0">Cancel Appointment</h4>
          <p class="text-muted mb-0 mt-1 small">
            Cancel your booked appointment
          </p>
        </div>
        <hr />
        <b-row lg="12" class="pt-3">
          <b-col lg="6">
            <h4 class="card-header-title">
              Event Owner
            </h4>
          </b-col>
          <b-col lg="6">
            <span class="text-muted">
              {{ eventTypeData.name }}
            </span>
          </b-col>
        </b-row>
        <b-row lg="6" class="pt-3">
          <b-col lg="6">
            <h4 class="card-header-title">
              Event Name
            </h4>
          </b-col>
          <b-col lg="6">
            <span class="text-muted">
              {{ eventTypeData.title }}
            </span>
          </b-col>
        </b-row>
        <b-row lg="12" class="pt-3">
          <b-col lg="6">
            <h4 class="card-header-title">
              Description
            </h4>
          </b-col>
          <b-col lg="6">
            <span class="text-muted" v-html="eventTypeData.description"></span>
          </b-col>
        </b-row>
        <b-row lg="12" class="pt-3">
          <b-col lg="6">
            <h4 class="card-header-title">
              Status
            </h4>
          </b-col>
          <b-col lg="6">
            <span class="text-muted">
              {{ eventTypeData.status }}
            </span>
          </b-col>
        </b-row>
        <b-row lg="12" class="pt-3">
          <b-col lg="6">
            <h4 class="card-header-title">
              Appointment Date / Time
            </h4>
          </b-col>
          <b-col lg="6">
            <span class="text-muted" v-if="eventTypeData.start_time">
              {{ moment(moment.utc(eventTypeData.start_time)).format("llll") }}
            </span>
          </b-col>
        </b-row>
        <b-row lg="12" class="pt-3" v-if="cancelEventStatus">
          <b-col>
            <h4 class="card-header-title">
              Cancellation Reason
            </h4>
            <b-form @submit.prevent="cancelEvent($event)">
              <b-form-input v-model="form.cancelReason" placeholder="Enter cancellation reason" required trim></b-form-input>
              <hr />
              <button class="btn btn-primary float-right p-2" type="submit">
                Confirm
              </button>
            </b-form>
          </b-col>
        </b-row>
      </b-card-body>
      <b-card-body v-else><h3 class="text-center">Sorry! Your URL is Broken</h3></b-card-body>
    </b-card>
    <div v-else>
      <Confirmation :cancelStatus="true" />
    </div>
    <b-overlay :show="isBusy" no-wrap opacity="0.5"></b-overlay>
  </div>
</template>

<script>
  import Schedulely from "@/js/schedulely/schedulely";
  import Confirmation from "./Confirmation";
  import moment from "moment";

  export default {
    name: "CancelEvent",
    components: {
      Confirmation,
    },
    data() {
      return {
        domain: "",
        eventTypeData: {},
        cancelEventStatus: false,
        showCancelText: "",
        form: {
          cancelReason: "",
        },
        pageError: false,
        showConfirm: false,
        isBusy: false,
      };
    },
    mounted() {
      this.domainId = window.domain;
      if (this.$route.query && this.$route.query.email) this.getEventTypeData();
      else this.pageError = true;
    },
    methods: {
      // Get Event Type Data
      async getEventTypeData() {
        try {
          this.isBusy = true;
          var data = { email: this.$route.query.email };
          let response = await Schedulely.add(`${this.domainId}/${this.$route.params.user_id}/${this.$route.params.event_id}/events`, data);
          if (response.length > 0) {
            this.eventTypeData = response[0];
            let start_time = new Date(this.eventTypeData.start_time);
            start_time = start_time.setMinutes(start_time.getMinutes() - moment().utcOffset());
            if (start_time > new Date().getTime() && this.eventTypeData.status == "scheduled") this.cancelEventStatus = true;
          } else {
            this.cancelEventStatus = false;
          }
          this.isBusy = false;
        } catch (error) {
          console.log(error);
          this.pageError = true;
          this.isBusy = false;
        }
      },
      async cancelEvent() {
        this.isBusy = true;
        if (this.eventTypeData.status === "cancel") {
          this.showCancelText = "This appointment has been already cancelled.";
        } else {
          var data = { status: "cancelled", event_id: this.$route.params.event_id, email: this.$route.query.email, cancel_reason: this.form.cancelReason };
          await Schedulely.edit(this.$route.params.event_id, `${this.domainId}/${this.$route.params.user_id}/cancelEvent`, data);
          this.showConfirm = true;
          this.isBusy = false;
        }
      },
    },
  };
</script>

<style></style>
